.yachts-banner {
    background: 
    -webkit-gradient(
      linear, 
      left top, left bottom, 
      from(rgba(1, 13, 22, 0.60)), 
      to(rgba(1, 13, 22, 0.60))
    ),
    url("https://www.lawsonrobb.com/wp-content/uploads/2015/02/superyacht_interior_design_stern-2400x1368.jpg") no-repeat center center fixed;
    background: 
    -webkit-linear-gradient(
      rgba(1, 13, 22, 0.60), 
      rgba(1, 13, 22, 0.60)
    ),
    url("https://www.lawsonrobb.com/wp-content/uploads/2015/02/superyacht_interior_design_stern-2400x1368.jpg") no-repeat center center fixed;
    background: 
    -o-linear-gradient(
      rgba(1, 13, 22, 0.60), 
      rgba(1, 13, 22, 0.60)
    ),
    url("https://www.lawsonrobb.com/wp-content/uploads/2015/02/superyacht_interior_design_stern-2400x1368.jpg") no-repeat center center fixed;
    background: 
    linear-gradient(
      rgba(1, 13, 22, 0.60), 
      rgba(1, 13, 22, 0.60)
    ),
    url("https://www.lawsonrobb.com/wp-content/uploads/2015/02/superyacht_interior_design_stern-2400x1368.jpg") no-repeat center center fixed;
    background-size: cover;
    height: 30em;
    width: 100%;
}

.yachts-description-container {
    padding: 7% 0% 0% 30%;
}

.yachts-banner-description {
    font-family: 'Cinzel', serif;
    line-height: 75px;
    text-align: center;
    font-size: 48px;
    color: white;
    width: 500px;
}

.yachts-page-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-left: 3%;
    margin-right: 2%;
}

.yachts-description {
    color: black;
    font-family: 'Source Serif Pro', serif;
    font-weight: 400;
    text-align: center;
    margin: 0;
}

.yachts-picture-group {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.display-image {
    height: 30em;
    width: 40em;
    margin: 2em;
}


